.navbar {
    background-color: #05800b;
    overflow: hidden;
  }
  
  .navbar-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  .navbar-item {
    flex: 1;
  }
  
  .navbar-link {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  .navbar-link:hover {
    background-color: #575757;
  }